.container{
  .burgerPages {
    display: none;

    .openedBurgerPage {
      width: 100vw;
      height: 60px;
      background-color: #638892;
      color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 35px;
      //position: relative;
      z-index: 120;
    }
    .burgerMenuButton{
      position: absolute;
      top: 18px;
      left: 20px;
      height: 25px;
      width: 30px;
      z-index: 200;
      border: 0;
      border-radius: 5px;
      background-color: transparent;
      padding: 0;


      .topLine,.middleLine,.bottomLine{
        display: block;
        height: 2px;
        width: 100%;
        background-color: white;
        transition: all 0.2s linear;
        border-radius: 1px;

      }
      .topLine{

        position: absolute;
        top: 0;
      }
      .middleLine{
        //position: absolute;
        scale: 1;
      }

      .bottomLine{

        position: absolute;
        bottom: 0;

      }
    }
    .burgerMenuButtonActive{

      .topLine{
        rotate: -45deg;
        top: 46%;

      }
      .middleLine{
        scale: 0;
        opacity: 0;

      }

      .bottomLine{
        rotate: 45deg;
        bottom: 47%;


      }

    }
    .burgerMenu{
      height: 100vh;
      width: 100%;
      background-color: #638892;
      position: absolute;
      top: -100vh;
      left: 0;
      transition: top 0.2s linear;
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 40px;
      color: white;
      .burgerButtons{
        margin-top: 20%;
        width: 80%;
        .burgerButton{
          color: white;
          cursor: default;
        }
        .line{
          display: block;
          width: 100%;
          height: 3px;
          background-color: white;
        }
      }
    }
    .active{
      top: 0;
    }
  }

  .pagesButton {
    position: relative;
    background-color: transparent;
    height: 70px;
    display: flex;
    justify-content: space-between;
    max-width: 1280px;
    width: 100%;




  }
}



//максимальный размер header
@media (max-width: 820px) {
  .container{
    .burgerPages {
      display: block;
      position: fixed;
      left: 0;
      top: 0;
      z-index: 101;
    }

    .pagesButton {
      display: none;
    }
  }

}
