.container {
  height: 100%;
  display: flex;
  .programmers{
    width: 595px;
    background-color: #638892;

  }
  .chat{
    width: 100%;
    height: 650px;
    display: flex;
    flex-direction: column;


  }
}
@media (max-width: 820px) {
  .container{
    padding-top: 60px;
    height: 100vh;
    .programmers{
      display: none;
    }
    .chat{
      height: 100%;
    }
  }
}